import axios from 'axios'


axios.interceptors.request.use(
        config => {
            return config
        },
        error => {
            console.error(error) // for debug 11
            Promise.reject(error)
        }
    )
    // response 
axios.interceptors.response.use(
    response => {
        return response
    },
    error => {
        // TODO
        console.error(error) // for debug 11
    }
)
export default axios