import { Graph } from "@antv/x6";
import React, { useState, useEffect } from "react";
import { forEach } from "lodash";

interface GraphProps {
  policy: any;
  children?: any;
}

export default function (props: GraphProps) {
  /**
   * first: 
   * start:
   */

  /**
   * 
   * 
   *     
   *     
   *     
   *     
   * Graph: width height
   * node: {
        id: "node1", // String
        x: 40, // Number x 
        y: 40, // Number y 
        width: 80, // Number width 
        height: 40, // Number height 
        label: "hello", // String
      }
     edges: {
        source: "node1", // String id
        target: "node2", // String id
        labels: [
          {
            attrs: { label: { text: "edge" } },
          },
        ],
      },
      policyMaps, bestPyramid, betterPyramids, nodesMap bestPyramid.maxWidth 
      
            1.
                  2.bestPyramid.pyramid id
                  3.nodesMap  
   */
  const nodes: any = [];
  props.policy.bestPyramid.pyramid.forEach((layer: any, yIndex: number) => {
    // x 40 node 100    y 60 node60
    layer.forEach((node: any, xIndex: number) => {
      if (!node) return;
      let body:any = {
        fill: '#FFFFFF',
        stroke: '#D4D4D4',
        rx: 8,
        ry: 8,
        zIndex: 1
      }
      if(node.isAuth){
        body.fill = '#E5F6EF'
        body.stroke = '#8FD6B8'
      }
      nodes.push({
        id: node.status, // String
        x: xIndex * 100 + xIndex * 80 + 10, // Number x 
        y: yIndex * 60 + yIndex * 60 + 10, // Number y 
        width: 100, // Number width 
        height: 60, // Number height 
        label: node.status, // String
        zIndex: 1000,
        attrs: {
          body
        },
      });
    });
  });
  const edges: any = []
  // TODO 
  props.policy.translateInfo.fsmInfos.forEach((info:any)=>{
    info.eventTranslateInfos.forEach((eventInfo:any)=>{
      eventInfo.used = false      
    })
  })
  props.policy.nodesMap.forEach((node:any, key:any)=>{
    // source  target 
    // target
    //             , ytargetsourcex
    //             
    node.from.forEach((item:any)=>{
      let text = ''
      props.policy.translateInfo.fsmInfos.forEach((info:any)=>{
        if(info.stateInfo.origin === item){
          info.eventTranslateInfos.forEach((eventInfo:any)=>{
            if(eventInfo.origin.state === key){
              eventInfo.used = true
              text = eventInfo.content.substr(0, eventInfo.content.lastIndexOf(''))
            }
          })
        }
      })
      edges.push({
        source: item, // String id
        target: key, // String id
        labels: [
          {
            attrs: { label: { text } },
          },
        ],
        attrs: {
          line: {
            stroke: "#979797", //  path 
          },
        },
        // connector: {
        //     name: 'rounded',
        //     args: {},
        //   },
        // vertices: [
        //   { x: 300, y: 180 },
        // ],
      })
    })
    node.to.forEach((item:any)=>{
      let text = ''
      props.policy.translateInfo.fsmInfos.forEach((info:any)=>{
        if(info.stateInfo.origin === key){
          info.eventTranslateInfos.forEach((eventInfo:any)=>{
            if(eventInfo.origin.state === item){
              eventInfo.used = true
              text = eventInfo.content.substr(0, eventInfo.content.lastIndexOf(''))
            }
          })
        }
      })
      edges.push({
        source: key, // String id
        target: item, // String id
        labels: [
          {
            attrs: { label: { text } },
          },
        ],
        attrs: {
          line: {
            stroke: "#979797", //  path 
          },
        },
        // connector: {
        //     name: 'rounded',
        //     args: {},
        //   },
        // vertices: [
        //   { x: 300, y: 180 },
        // ],
      })
    })
  })
  const data = {

    // 
    edges: [
      ...edges
      // {
      //   source: "node2", // String id
      //   target: "node1", // String id
      //   labels: [
      //     {
      //       attrs: { label: { text: "edge33" } },
      //     },
      //   ],
      //   connector: {
      //       name: 'rounded',
      //       args: {},
      //     },
      //   vertices: [
      //     { x: 300, y: 180 },
      //   ],
      // }
    ],
        // 
        nodes,
  };
  useEffect(() => {
    const graph = new Graph({
      // @ts-ignore
      container: document.getElementById(props.policy.policyId),
      width: 800,
      height: 600,
      // frozen: true,
      panning: false,
      interacting: {
        nodeMovable: false,
        edgeMovable: false,
      },
    });
    graph.fromJSON(data);
  });

  return (
    <div className="w-100x h-100x x-auto y-auto ">
      <div id={props.policy.policyId} className="  w-100x  x-auto y-auto"></div>
    </div>
  );
}
