import { SUCCESS, FAILED, USER_CANCEL } from "./event";
import { LOGIN, CONTRACT } from "./event";
/**
 * 
 *     1.
 *     2.
 *           UI
 *           UIUI
 *  apiUIUI
 *   
 *  UI 1.   2.  3.ui
 *           
 *                    UI
 *           UiUi
 * UI(s) UIUI
 *         UI UI
 *         over UI
 * Ui
 * UI
 * 31
 * 3
 */
export const presentableQueue = new Map<any, any>();
export const eventMap = new Map<any, any>(); // 
export const failedMap = new Map<any, any>();
let UI: any = null;
let updateUI: any = null;
export function reisterUI(ui: any, update: any) {
  UI = ui;
  updateUI = update;
}
let locked = false;
export function updateLock(status: boolean) {
  locked = !!status;
}
export function setPresentableQueue(name: string, value: any) {
  presentableQueue.set(name, value);
}
let uiInited = false;
// UI 
export function addAuth(
  presentableId: any,
  resolve: any,
  reject: any,
  options?: any
) {
  if (typeof resolve !== "function") {
    resolve = () => {};
  }
  if (typeof reject !== "function") {
    reject = () => {};
  }
  let event;
  // @ts-ignore
  const name = this.name;
  if (name !== "node") {
    // @ts-ignore
    let data = presentableQueue.get(presentableId);
    if (!data) {
      //  TODO 
      reject &&
        reject({
          errorCode: 2,
          msg: presentableId + " is inncorrect or not required for callUI",
        });
      return;
    }
    // TODO  errCode  
    if (data.errCode === 3 && data.authCode === 502 && false) {
      event = LOGIN;
    } else {
      event = CONTRACT;
    }
    const arr = eventMap.get(presentableId)?.callBacks || [];
    arr.push({
      resolve,
      reject,
      options,
    });
    let id = presentableId;
    if (event === LOGIN) id = event;
    eventMap.set(id, {
      event, // UI
      eventId: id, // evnetIdprsesentableId
      ...data,
      callBacks: arr,
    });
    if (options && options.immediate) {
      if (!uiInited) {
        UI && UI();
      } else {
        if (locked) {
          setTimeout(() => {
            updateUI && updateUI();
          }, 0);
        } else {
          updateUI && updateUI();
        }
      }
    }
    uiInited = true;
    return;
  }
  eventMap.set(name, {
    // @ts-ignore
    event: this.event,
    // @ts-ignore
    eventId: name, // evnetIdprsesentableId
    presentableId,
    callBacks: [
      {
        resolve,
        reject,
        options,
      },
    ],
  });
  UI && UI();
  uiInited = true;
}
export function callAuth() {
  if (!uiInited) {
    UI && UI();
  } else {
    if (locked) {
      setTimeout(() => {
        updateUI && updateUI();
      }, 0);
    } else {
      updateUI && updateUI();
    }
  }
}
export function updateEvent(event:any) {
  if(!event) return eventMap
  eventMap.set(event.eventId, event)
  return eventMap
}
function removeEvent(eventId?: string) {
  if(eventId){
    eventMap.delete(eventId);
  }else{
    eventMap.clear()
  }
  if (locked) {
    setTimeout(() => {
      updateUI && updateUI();
    }, 0);
  } else {
    updateUI && updateUI();
  }
}
export function endEvent(eventId: string, type: number, data: any) {
  // if (eventMap.get(eventId)) {
    // TODO 
    switch (type) {
      case SUCCESS:
        eventMap.get(eventId).callBacks.forEach((item: any) => {
          item.resolve(data);
        });
        presentableQueue.delete(eventId);
        removeEvent(eventId);
        break;
      case FAILED:
        eventMap.get(eventId).callBacks.forEach((item: any) => {
          item.reject(FAILED, data);
        });
        presentableQueue.delete(eventId);
        removeEvent(eventId);
        break;
      case USER_CANCEL:
        eventMap.forEach((event:any)=>{
          event.callBacks.forEach((item: any) => {
            item.reject(USER_CANCEL, data);
          });
        }) 
        presentableQueue.clear();
        removeEvent();
        console.log(presentableQueue)
        break;
    }
  // }
}
