import user from "../../../services/api/modules/user";
import frequest from "../../../services/handler";
import {
  checkPhone,
  checkEmail,
  checkPassword,
  checkUsername,
} from "../../../utils/utils";
import { Tabs, Badge, Modal, Button, Toast } from "antd-mobile";

import { useState, useEffect } from "react";
import "./register.scss";

interface loginProps {
  visible: boolean;
  setModalType: any;
  children?: any;
}
export default function (props: loginProps) {
  const [errorTip, setErrorTip] = useState<any>({
    username: "",
    loginName: "",
    phone: "",
    email: "",
    password: "",
    authCode: "",
  });
  const [success, setSuccess] = useState(false);
  const [loading, setLoading] = useState(false);
  const [available, setAvailable] = useState(false);
  const [countDown, setCountDown] = useState(60);
  const [authCode, setAuthCode] = useState("");
  const [authCodeLoading, setAuthCodeLoading] = useState(false);
  const [username, setUsername] = useState("");
  // loginName: phone | email
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  // 1 phone    2 email
  const [registerType, setRegisterType] = useState(1);
  const verify = (type: any, value: any) => {
    value = value ? value : "";
    const obj: any = {
      [type]: "",
    };
    if (type === "username" && !checkUsername(value)) {
      obj[type] = value
        ? "30-"
        : "";
    }
    if (type === "phone" && !checkPhone(value)) {
      obj[type] = value ? "" : "";
    }
    if (type === "email" && !checkEmail(value)) {
      obj[type] = value ? "" : "";
    }
    if (type === "password" && !checkPassword(value)) {
      obj[type] = value
        ? "6-24"
        : "";
    }
    if (type === "authCode") {
      obj[type] = value ? "" : "";
    }
    const errors = {
      ...errorTip,
      ...obj,
    };
    setErrorTip(errors);
    const values = {
      username,
      password,
      authCode,
      [type]: value,
      loginName: registerType === 1 ? phone : email,
    };
    const flag1 = Object.keys(errors).some((key: any) => {
      if (errors[key]) {
        return true;
      }
    });
    const flag2 = Object.keys(values).some((key: any) => {
      if (!values[key]) {
        return true;
      }
    });
    setAvailable(!(flag1 || flag2));
  };
  useEffect(() => {
    if (!authCodeLoading) return;
    const timer = window.setInterval(() => {
      if (countDown <= 0) {
        window.clearInterval(timer);
        setAuthCodeLoading(false);
        setCountDown(60);
        return;
      }
      setCountDown((prevTime) => {
        if (prevTime <= 0) {
          window.clearInterval(timer);
          setAuthCodeLoading(false);
          return 60;
        }
        return prevTime - 1;
      }); // <-- Change this line!
    }, 1000);
    return () => {
      window.clearInterval(timer);
    };
  }, [authCodeLoading]);
  const getAuthCode = async () => {
    // {
    //     "loginName":"18923803593",
    //     "authCodeType":"register"
    // }
    const authCodeRes = await frequest(user.getAuthCode, "", {
      loginName: registerType === 1 ? phone : email,
      authCodeType: "register",
    });
    if (authCodeRes.errCode !== 0) {
      const obj: any = {};
      obj[registerType === 1 ? "phone" : "email"] = authCodeRes.data.msg;

      setErrorTip({
        ...errorTip,
        ...obj,
      });
    }
  };
  const onFinish = async () => {
    setLoading(true);
    const values: any = {
      loginName: registerType === 1 ? phone : email,
      username,
      password,
      authCode,
    };
    //   username: "string",
    //   password: "string",
    //   isRemember: "string",
    //   returnUrl: "string",
    //   jwtType: "string",
    const res = await frequest(user.postRegister, "", values);
    if (res.data.errCode === 0) {
      setSuccess(true);
      setLoading(false);
    } else {
      Toast.fail(res.data.msg, 2);
      if (res.data.msg.startWith("")) {
        const obj: any = { authCode: res.data.msg };
        setErrorTip({
          ...errorTip,
          ...obj,
        });
      }
      setTimeout(() => setLoading(false), 2000);
    }
  };

  return (
    <Modal
      popup
      visible={props.visible}
      maskClosable={false}
      animationType="slide"
      className="w-100x h-100x"
      wrapClassName="user-register"
    >
      <div className="w-100x h-100x flex-column align-center">
        <div className="flex-1 w-100x flex-column align-center">
          <div className="register-title mb-38 mt-45 flex-row px-30 self-start">
            
          </div>
          <div className="register-type mb-30  flex-row px-30 self-start align-center">
            <input
              type="radio"
              id="phone-type"
              name="register-type"
              className="mr-4"
              checked={registerType === 1}
              value="1"
              onChange={(e) => {
                setRegisterType(parseInt(e.target.value));
              }}
            />{" "}
            <label
              htmlFor="phone-type"
              className={registerType === 1 ? "selected mr-20" : " mr-20"}
            >
              
            </label>
            <input
              type="radio"
              id="mail-type"
              name="register-type"
              className="mr-4"
              checked={registerType === 2}
              onChange={(e) => {
                setRegisterType(parseInt(e.target.value));
              }}
              value="2"
            />{" "}
            <label
              htmlFor="mail-type"
              className={registerType === 2 ? "selected" : ""}
            >
              
            </label>
          </div>
          <div className="register-container flex-column justify-center px-30">
            <input
              type="text"
              className="w-100x mb-5"
              value={username}
              placeholder=""
              onChange={(e) => {
                verify("username", e.target.value);
                setUsername(e.target.value);
              }}
            />
            {errorTip.username !== "" ? (
              <div className="error-tip self-start">{errorTip.username}</div>
            ) : null}
            {registerType === 1 ? (
              <input
                type="text"
                value={phone}
                className="w-100x  mb-5 mt-15"
                placeholder={""}
                onChange={(e) => {
                  verify("phone", e.target.value);
                  setPhone(e.target.value);
                }}
              />
            ) : (
              <input
                type="text"
                value={email}
                className="w-100x  mb-5 mt-15"
                placeholder={""}
                onChange={(e) => {
                  verify("email", e.target.value);
                  setEmail(e.target.value);
                }}
              />
            )}
            {errorTip[registerType === 1 ? "phone" : "email"] !== "" ? (
              <div className="error-tip self-start">
                {errorTip[registerType === 1 ? "phone" : "email"]}
              </div>
            ) : null}
            <div className="flex-row mb-5 mt-15">
              <input
                type="text"
                value={authCode}
                className="mr-10"
                placeholder=""
                onChange={(e) => {
                  verify("authCode", e.target.value);
                  setAuthCode(e.target.value);
                }}
              />
              <Button
                loading={loading}
                type="primary"
                className="flex-1 shrink-0 fs-16"
                disabled={
                  authCodeLoading ||
                  (registerType === 1
                    ? !phone || errorTip.phone
                    : !email || errorTip.email)
                }
                onClick={() => {
                  getAuthCode();
                  setAuthCodeLoading(true);
                  setCountDown(60);
                }}
              >
                {authCodeLoading ? (
                  <span> {countDown}s</span>
                ) : (
                  ""
                )}
              </Button>
            </div>
            <input
              type="password"
              value={password}
              className="w-100x  mt-15 mb-5"
              placeholder=""
              onChange={(e) => {
                verify("password", e.target.value);
                setPassword(e.target.value);
              }}
            />
            {errorTip.password !== "" ? (
              <div className="error-tip  self-start">{errorTip.password}</div>
            ) : null}
            <Button
              loading={loading}
              type="primary"
              className="mt-15"
              onClick={onFinish}
              disabled={!available}
            >
              {loading ? "" : " "}
            </Button>
          </div>
        </div>

        <div className="flex-row justify-center align-center register-bottom mb-50">
          
          <Button
            type="ghost"
            inline
            size="small"
            onClick={() => props.setModalType(1)}
          >
            
          </Button>
        </div>
      </div>
      <Modal
        visible={loading}
        transparent
        maskClosable={false}
        title=""
        className="w-325 h-220 register-tip"
      >
        <div className="paying bg-white">
          <Button loading className="loading">
            
          </Button>
        </div>
      </Modal>
      <Modal
        popup
        visible={success}
        maskClosable={false}
        animationType="slide"
        className="w-100x h-100x"
        wrapClassName="register-success"
      >
        <div className="w-100x h-100 flex-column justify-center">
          <div className="flex-column align-center ">
            <i className="iconfont ">&#xe62d;</i>
            <span className=" success mb-60 mt-4"></span>
            <div className="flex-row justify-center align-center">
              <span className="count-back">3s</span>
              <Button
                type="ghost"
                inline
                size="small"
                onClick={() => {
                  setSuccess(false);
                  props.setModalType(1);
                }}
              >
                
              </Button>
            </div>
          </div>
        </div>
      </Modal>
    </Modal>
  );
}
