import { addAuth } from "../../bridge/index";
// 
export function asyncCallUI(
  presentableId: any,
  resolve: any,
  reject: any,
  options?: any
) {
  // @ts-ignore
  addAuth.bind(this)(presentableId, resolve, reject, options);
}
//   callBack callUI callBack
// 
export async function callUI(
  presentableId: any,
  resolve: any,
  reject: any,
  options?: any
) {
  await new Promise((res, rej) => {
    const _res = () => {
      // @ts-ignore
      resolve && resolve(...arguments);
      // @ts-ignore
      res && res(...arguments);
    };
    const _rej = () => {
      // @ts-ignore
      reject && reject(...arguments);
      // @ts-ignore
      rej && rej(...arguments);
    };
    // @ts-ignore
    addAuth.bind(this)(presentableId, _res, _rej, options);
  });
}
export const loginCallback:any = []
// 
export async function onLogin(
  callback: any 
) {
  if(typeof callback === 'function'){
    loginCallback.push(callback)
  } else{
    console.error('onLogin error: ' , callback, ' is not a function!')
  }
}