import user from "../../../services/api/modules/user";
import frequest from "../../../services/handler";
import { SUCCESS } from "../../../bridge/event";
import { Tabs, Badge, Modal, Button, Toast } from "antd-mobile";

import { useState } from "react";
import "./forgot.scss";

 

interface loginProps {
  visible: boolean;
  setModalType: any;
  children?: any;
}
export default function (props: loginProps) {
  const [errorTip, setErrorTip] = useState<any>({
    loginName: "",
    password: "",
  });
  const [logging, setLogging] = useState(false);
  const [loginName, setLoginName] = useState("");
  const [password, setPassword] = useState("");
  const onFinish = async () => {
    setLogging(true);
    const values: any = {
      loginName,
      password,
    };
    // loginName: "string",
    //   password: "string",
    //   isRemember: "string",
    //   returnUrl: "string",
    //   jwtType: "string",
    values.isRemember = values.isRemember ? 1 : 0;
    const res = await frequest(user.login, "", values);
    if (res.data.errCode === 0) {
      setLogging(false);
    } else {
      Toast.fail(res.data.msg, 2);
    }
  };

  return (
    <Modal
      popup
      visible={props.visible}
      maskClosable={false}
      animationType="slide"
      className="w-100x h-100x"
      wrapClassName="user-forgot"
    >
      <div className="w-100x h-100x flex-column align-center">
        <div className="flex-1 w-100x flex-column align-center">
          <i className="iconfont  mt-50 mb-20 logo">&#xe614;</i>

          <div className="login-title mb-46 flex-row justify-center">
            
          </div>
          <div className="login-container flex-column justify-center">
            <input
              type="text"
              value={loginName}
              placeholder="//"
              onChange={(e) => {
                setLoginName(e.target.value);
              }}
            />
            <input
              type="password"
              value={password}
              placeholder=""
              onChange={(e) => {
                setPassword(e.target.value);
              }}
            />
            <Button
              loading={logging}
              type="primary"
              className="mb-15"
              onClick={onFinish}
              disabled={!loginName || !password}
            >
              {logging? '' : ' '}
            </Button>
            <Button className="registry"> </Button>
          </div>
        </div>

        <div className="text-center login-forgot mb-50"></div>
      </div>
    </Modal>
  );
}
